body {
    margin: 0;
    font-family: "Trebuchet MS", sans-serif;
}

h1 {
    margin-bottom: 0;
}

h2 {
    margin-top: 0;
}

h3 {
    color: #50a3d9;
    margin-bottom: 0.2rem;
}

h1 ~ div>h3, h1 ~ section>h3 {
    margin-top: 0.2rem;
}

h4 {
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
}

p, ul {
    margin-bottom: 1rem;
    margin-top: 0.2rem;
}
p:empty {
    margin-bottom: 1rem;
}

a:-webkit-any-link {
    text-decoration: none;
    color: inherit;
}

a:-moz-any-link {
    text-decoration: none;
    color: inherit;
}

a:any-link {
    text-decoration: none;
    color: inherit;
}

section {
    margin-bottom: 1rem;
    padding-left: 1rem;
    margin-left: 0.2rem;
    border-left: 1px solid #50a3d9;
}

.containerStyle {
    text-align: center;
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 250px 1fr;
    grid-template-areas:
            "aside header"
            "aside content"
}

@media print {
    .containerStyle {
        grid-template-columns: 200px 1fr;
    }

    a[href]:after {
        content: attr(href);
        font-size: 0.6rem;
    }

    #innerLinks {
        display: none;
    }
}


